import React ,{Component} from 'react';
import { 
    Form, 
    Input, 
    Button, 
    Breadcrumb ,
    Row,
    Col
  } from 'antd';
  import '../../../style/projectdongtaiadd.scss'
  import axios from 'axios'
class DongtaiForm extends Component{
      constructor(props){
        super(props)
        this.state={
             detail:[],
             gid:'',
        }
      }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('输入的结果为 ', values);
            var key=sessionStorage.getItem('key');
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            console.log(loginid,user);
          //   if(loginid!=='' && user!==""){
          //   axios.post('/index.php/guide/save',{
          //       bid:key,
          //       value:values,
          //       name:user,
          //       num:loginid
          //   })
          //   .then((res)=>{
          //         if(res.data.code==200){
          //           console.log(res);
          //           this.props.history.push('/projectmanage/projectInformation/projectlist');
          //         }
          //   }).catch((error)=>{
          //     console.log(error)
          //   })
          // }
          }
        });
      };
       componentDidMount(){
            this.getDetail();
       }
       getDetail(){
        var key=sessionStorage.getItem('key');
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        if(loginid!=='' && user!==""){
          axios.post('/index.php/guide/edit',{
              id:key,
              name:user,
              num:loginid
          })
          .then((res)=>{
                if(res.data.code==200){
                  console.log(res.data.data);
                  var num=res.data.data;
                  if(num.length>0){
                    this.setState({
                      detail:res.data.data,
                      // gid:res.data.data.gid
                   })
                  }
                   console.log(this.state.gid);
                
                 
                }
          }).catch((error)=>{
            console.log(error)
          })
        }


       }
       delete(num){
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
            console.log(num);
            if(loginid!=='' && user!==""){
              axios.post('/index.php/guide/delete',{
                  id:num,
                  name:user,
                  num:loginid
              })
              .then((res)=>{
                    if(res.data.code==200){
                      console.log(res)
                     this.getDetail();
                    }
              }).catch((error)=>{
                console.log(error)
              })
            }
       }
       update(e){
        var loginid=localStorage.getItem('loginid');
        var user=localStorage.getItem('name');
        var key=sessionStorage.getItem('key');
        console.log(e.target);
        var id=e.target.getAttribute('data-id');
        console.log(id);
        var gid=e.target.getAttribute('data-gid');
        console.log(gid);
        console.log(e.target.parentNode.children[0].children[0].children[0].children[0].children[0].value);
        var dom=e.target.parentNode.children[0].children[0].children[0].children[0].children[0].value
        if(loginid!=='' && user!==""){
          axios.post('/index.php/guide/update',{
              gid:gid,
              id:id,
              name:user,
              num:loginid,
              introduce:dom
          })
          .then((res)=>{
                if(res.data.code==200){
                  console.log(res)
                 this.getDetail();
                }
          }).catch((error)=>{
            console.log(error)
          })
        }

       }
      render(){
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        const { detail } =this.state;
        return(
          <div className="projectdongtaiadd">
                 <div className="bread">
                    <Breadcrumb>
                        <Breadcrumb.Item href="/"> 项目管理</Breadcrumb.Item>
                        <Breadcrumb.Item href="">项目列表 </Breadcrumb.Item>
                        <Breadcrumb.Item href="">编辑动态 </Breadcrumb.Item>
                    </Breadcrumb>
                    </div>
                    <Form layout="inline" onSubmit={this.handleSubmit}>
                       <p className="title2">动态内容</p>
                       {
                             detail.length>0 ? detail.map((m,n)=>{
                                    return(
                                      <Row key={n} gutter={24}>
                                        
                                          <Form.Item >
                                          {getFieldDecorator('introduce', {
                                              initialValue: m.introduce,
                                                  })(
                                              <TextArea rows={4}   autosize={{ minRows: 4, maxRows: 6 }} placeholder="请输入动态内容" style={{}}/>
                                                  )}
                                          </Form.Item>
                                 
                                      <Button type="primary" onClick={this.delete.bind(this,m.id)} style={{marginRight:'20px'}}>
                                            删除
                                      </Button>
                                      <Button type="primary"    onClick={this.update.bind(this)} style={{marginTop:'20px'}} data-id={m.id} data-gid={m.gid}>
                                          更新
                                      </Button>
                                   

                                      </Row>
              
                                    )
                             }) :'暂无动态'
                       }
                     

                        <Row>
                        <Form.Item>
                   
                     
                    </Form.Item>
                    </Row>
                    </Form>
          </div>
          )
      }
}


const ProjectDongTaiAdd = Form.create()(DongtaiForm);
export default ProjectDongTaiAdd
